import React from 'react';
import {
    Box,
    Input,
    Select,
    SimpleGrid,
    Stack,
    Switch,
    Text,
    Button,
    Center,
    cookieStorageManager
  } from '@chakra-ui/react';
  import axios from 'axios';

export default function Results(props) {
  const [result, setResult] = React.useState('');

  let getResults = async() => {
    axios.get(`http://ibethorses.com/request/api/v1/results?race_id=${props.raceId}`)
    .then(res => {setResult(res.data.results)})
  }
  getResults()
  var loopData = []

  for(var i=0;i<result.length; i++){
    loopData[i] = result[i]
  }
  
  return (
    <>
      <Box textAlign='center'>
        <Box borderWidth='1px' borderRadius='lg' p='2' marginRight='3'>
        <Text fontSize="20"><b>Race Results</b></Text>
        <SimpleGrid columns={4} spacing={0} fontSize='sm'>
          <Text>#</Text>
          <Text backgroundColor='#FFF09E' width='60%'>Win</Text>
          <Text backgroundColor='#E7E7E7' width='60%'>Place</Text>
          <Text backgroundColor='#ECCEB1' width='60%'>Show</Text>
        </SimpleGrid>
          {loopData.map(obj=>
            <SimpleGrid columns={4} spacing={0} fontSize='sm'>
              <Text>{obj.pgm}</Text>
              <Text width='60%'>{obj.wps_win}</Text>
              <Text width='60%'>{obj.wps_place}</Text>
              <Text width='60%'>{obj.wps_show}</Text>
            </SimpleGrid> 

            )}
        
        </Box>

      </Box>
    </>
  );
}